<template>
    <section class="layout">

        <Form v-if="!isAuthorized">
            <h1 class="title">Вход</h1>

            <div class="item">
                <MDInput label="Почта" ref="login" @input="onInput"></MDInput>
            </div>

            <div class="item">
                <MDPasswordInput label="Пароль" ref="password" @input="onInput"></MDPasswordInput>
            </div>

            <div class="item" v-if="isForbidden">
                <p class="error">Ваш аккаунт еще не авторизован. Свяжитесь с ДонОптТорг для выяснения деталей</p>
            </div>

            <div class="item" v-if="err.length !== 0">
                <p class="error">{{err}}</p>
            </div>

            <div class="item bottom-item">
                <button v-on:click="loginInit" v-if="!isAuthorizing">Войти</button>
                <LoadingSpinner v-if="isAuthorizing"/>
            </div>
        </Form>

        <Form  v-if="isAuthorized">
            <h1 class="title">Вы уже вошли в аккаунт</h1>
        </Form>
    </section>
</template>

<script>
    import { mapActions } from 'vuex';
    import modules from "../store/all.modules.name"
    import actions from "../store/all.actions.type";

    import MDInput from "./components/MD/MDInput";
    import MDPasswordInput from "./components/MD/MDPasswordInput";

    import jToken from "../common/jToken"
    import LoadingSpinner from "./components/LoadingSpinner";
    import Form from "./components/Form";

    const emailValidator = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    export default {
        name: "Login",

        components: {Form, LoadingSpinner, MDInput, MDPasswordInput},

        data() {
            return {
                isAuthorized: false,
                isAuthorizing: false,
                isForbidden: false,

                err: "",

                incorrectData: [],
            }
        },

        methods: {
            ...mapActions(modules.user, [
                actions.user.LOGIN,
                actions.user.LOGOUT,
            ]),

            onInput() {
                this.$refs.login.isValid = !this.$refs.login.isEmpty;
                if (this.$refs.login.isValid) {
                    this.$refs.login.isValid = emailValidator.test(String(this.$refs.login.data).toLowerCase())
                }

                this.$refs.password.isValid = !this.$refs.password.isEmpty;
                this.err = '';

                if (this.$refs.password.isValid) {
                    if (!this.$refs.login.isValid) {
                        return;
                    }

                    let res = this.incorrectData.find((item) => item.login === this.$refs.login.data
                        && item.password === this.$refs.password.data);
                    if (res) {
                        this.err = 'Введен неверный логин или пароль.';
                        this.$refs.login.isValid = false;
                        this.$refs.password.isValid = false;
                    } else if (this.$refs.password.data.length < 7) {
                        this.err = 'Пароль должен состоят минимум из 7 знаков.';
                        this.$refs.password.isValid = false;
                    }

                }
            },

            loginInit() {
                if (this.$refs.login.isValid && this.$refs.password.isValid) {
                    let login = this.$refs.login.data;
                    let password = this.$refs.password.data;

                    this.isAuthorizing = true;

                    this.login({login, password})
                        .then(status => {
                            this.isAuthorizing = false;

                            if (status === 200) {
                                this.$router.push({name: "home"})
                            } else if (status === 401) {
                                this.incorrectData.push({
                                    login,
                                    password,
                                });
                                this.onInput();
                            } else if (status === 403) {
                                this.isForbidden = true;
                            }
                        })
                        .catch(err => {
                            this.isAuthorizing = false;
                            console.error({err});
                            this.err = "Произошла ошибка на сервере. Попробуйте позже.";
                        })
                }
            },
        },

        computed: {
            isFormValid: function () {
                if (this.$refs.login && this.$refs.password) {
                    return this.$refs.login.isValid && this.$refs.password.isValid
                } else {
                    return false
                }
            }
        },

        mounted() {
            this.isAuthorized = jToken.has();

            if (this.isAuthorized) {
                this.onInput();
            }
        },
    }
</script>

<style lang="sass" scoped>
    .layout
        width: 90%
        max-width: 500px
        margin: auto
        font-family: Roboto, sans-serif
</style>